.extra_info {
    display: block;
    width: 100px;
    height: 100px;
    background-color: #ff0000;
    color: #ffffff;
    padding: 10px;

    text-decoration: none;

    position: fixed;
    bottom: 50px;
    right: 50px;

    font-family: Raleway;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-align: center;

    cursor: pointer;
}

.popup_extra_info {
    width: 600px;
    height: 300px;
    position: fixed;
    top: 30%;
    left: 30%;
    background-color: #181718;
    border: 10px solid red;
    display: none;
    z-index: 2;

    text-align: center;
}


.popup_extra_info_title {
    font-family: Playfair Display;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: 0em;
    
    color: #D4C17F;
}

.popup_extra_info_text {
    font-family: Raleway;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 27px;
    letter-spacing: 0em;

    color: #ffffff
}

.popup_extra_info_button {
    display: block;
    width: 200px;
    height: 50px;
    background-color: #D4C17F;
    padding: 10px;
    text-align: center;

    margin: 0 auto;

    cursor: pointer;
}